//cd lcotodo then npm start to start this project
import React from 'react';
import logo from "./logo_transparent.png"
import './App.css';

class App extends React.Component{       

  constructor(props){                    /* this is a constructor which can hold the default things here*/
    super(props);                     /* we use props if some other page is throwing some vales and those can be accessed using these props*/
    this.state={                       //this is the syntax we have to follow
      newItem : "",                    //new item is initially empty
                                                 //state is nothing more than an object
      list:[]                           //array of list which is going to store all the items that we have
    }
  }

  addItem(todoValue){              // this is a method addItem as soon as sombody hits add todo we are going to featch that value from input there and put it here
    if(todoValue !==""){
      const newItem={       //here we have created a new object
        id:Date.now(),               //to get unique id every time we need this unique id for each item to loop through it
        value:todoValue,                    
       
      };                               //now we have to copy this oblext to list:[] above in constructor

      const list=[...this.state.list];    //we are creating a value that is going to hold all the appended values
      list.push(newItem);

      this.setState({        //whenever we want to update anything in the state we use this method setState
        list:list, 
        newItem:""

      })
      
    }
  }
  deleteItem(id) {            //deleting the item
 const list=[...this.state.list];  
 const updatedlist= list.filter(item=>item.id !==id); //updating list 
 this.setState({list:updatedlist}) //what we are doing here is we are skipping the element which matches the id(have all items except whose id matches)
  }                                  //updating list item with updatedlist

updateInput(input){              //this is a method which expects you to pass a input
  this.setState({newItem:input})       //updating newItem with input  (we are using it to link above newItem in state top constructor)
}

  render(){                                //here we are using a method with return
  return (<div>
    <img src={logo} width="300" height="200" className="logo"/>
    <h1 className="app-title"> Todo App</h1>
     <div className="container">
      Add an item...
     <br />

     <input type="text" className="input-text" placeholder="write a todo"
      required
       value={this.state.newItem} //using this so that the value reaches the state in the top constructor
       onChange={e=>this.updateInput(e.target.value)}/> {/* e is for update we are getting the item and putting it to 
      updateInput() method which then is settingValue in its above method */}

     <button className="add-btn" 
     onClick={()=>this.addItem(this.state.newItem)} //use this. whenever calling a method
     >Add Todo</button> 


     <div className="list"><ul>   {/*now we are going to put loop to display items */}
      {
        this.state.list.map(item=>{
       return(<li key={item.id}>
        <input type="checkbox" name="isDone" checked={item.isDone}
        onChange={()=>{}} />
        {item.value}
        <button className="btn" onClick={()=>this.deleteItem(item.id)}>Delete</button>

        </li>);
     })}
     
     </ul></div>
     </div>
    
    </div>
    
     );
}
}
export default App;

